import Swiper from 'swiper';
import { onWindowResize } from 'utils/index';
import { BREAKPOINTS } from 'utils/constants';
import 'swiper/css';
import 'ScssComponents/latest-news.scss';
import 'ScssComponents/card-item.scss';
import 'ScssComponents/news-banner.scss';

const latestNews = (
	sectionSelector = '.js-latest-news-section',
	sliderSelector = '.js-latest-news-slider',
) => {
	const $sections = document.querySelectorAll(sectionSelector);
	if (!$sections.length) return;

	$sections.forEach(($section) => {
		const $slider = $section.querySelector(sliderSelector);
		if (!$slider) return;

		let isMobile;
		let latestSwiper;

		const initSlider = () => {
			latestSwiper = new Swiper($slider, {
				speed: 800,
				observer: true,
				observeParents: true,

				breakpoints: {
					320: {
						slidesPerView: 1.15,
					},
					480: {
						slidesPerView: 1.6,
					},
					768: {
						slidesPerView: 2.2,
					},
					1024: {
						slidesPerView: 3,
					},
				},
			});
		};

		const toggleSlider = () => {
			isMobile = window.innerWidth < BREAKPOINTS.mediaPoint1;
			if (isMobile && !$slider.classList.contains('swiper-initialized')) {
				initSlider();
			} else if (!isMobile && $slider.classList.contains('swiper-initialized')) {
				latestSwiper.destroy(true, true);
			}
		};

		onWindowResize(toggleSlider);
	});
};

export default latestNews;
